export default {
  data () {
    return {
      name: '',
      successMsg: ''
    }
  },
  mounted () {
    const $this = this;
    $this.name = $this.$route.query.userName;
    if($this.$route.params.successId == 1) {
      $this.successMsg = '欢迎嘉宾';
    } else if($this.$route.params.successId == 2) {
      $this.successMsg = '欢迎贵宾';
    }
    setTimeout(function () {
      $this.$vux.toast.show({
        text: '签到成功',
        width: '2.3rem',
        position: 'middle',
        time: 2000
      })
    },500)
  }
}



