export default {
  name: 'app',
  data () {
    return {
      transitionName: "slideInRight"
    }
  },
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.length;
      const fromDepth = from.path.length;
      this.transitionName = toDepth > fromDepth ? 'slideOutLeft' : 'slideOutRight';
    }
  }
}



